import React from 'react';
import { DebounceInput } from 'react-debounce-input';

export default function Input({
  onEnterPress,
  onChange,
  onKeyDown = null,
  ...props
}) {
  const handleKeyPress = (evt) => {
    if (
      evt.key === 'Enter'
      && onEnterPress
      && typeof onEnterPress === 'function'
    ) {
      onEnterPress(evt);
    }

    if (onKeyDown && typeof onKeyDown === 'function') {
      onKeyDown(evt);
    }
  };

  return (
    <DebounceInput
      minLength={2}
      debounceTimeout={300}
      onKeyDown={handleKeyPress}
      onChange={onChange}
      className="w-full md:flex-1 py-2 pl-2 border-b border-gray-400 focus:border-gray-900 text-sm text-gray-600 placeholder-gray-400 outline-none rounded"
      {...props}
    />
  );
}
