import React, { useContext } from 'react';
import { rangeBetween } from '../../../../utils/math';
import { StakingContext } from '../../../context/staking';
import { useWallet } from '../../../context/wallet';
import useStaking from '../../../hooks/useStaking';
import { Checkbox, Input, Label, Select } from '../../atoms';
import InputGroup from '../../atoms/input-group';

export function AddressBar() {
  const { setShowOnlyOwned, setStakingFromEpoch, stakingFromEpoch, showOnlyOwned } = useContext(StakingContext);
  const { address, setAddress } = useWallet();
  const { epochs } = useStaking();

  const epochOptions = rangeBetween(epochs.kickoff.start, epochs.kickoff.end);

  const handleOnChange = ({ target }) => {
    setAddress(target.value);
  }

  const handleOnEpochChange = ({ target }) => {
    setStakingFromEpoch(parseInt(target.value, 10));
  }

  const handleOnCheck = () => {
    setShowOnlyOwned(!showOnlyOwned);
  }

  return (
    <div className="md:flex mb-5">
      <div className="bg-stone-900 px-4 md:px-12 py-9 mb-5 rounded md:w-1/2 w-full md:mr-2">
        <InputGroup>
          <Label>Enter Bech32 Address (Starts with <i>stake</i>)</Label>
          <Input onChange={handleOnChange} onEnterPress={handleOnChange} value={address} />
        </InputGroup>
        <InputGroup>
          <Label>When did you start staking?</Label>
          <Select value={stakingFromEpoch} onChange={handleOnEpochChange} options={epochOptions} />
        </InputGroup>
      </div>
      <div className="bg-stone-900 px-2 py-9 mb-5 rounded w-full md:w-1/2 md:ml-2">
        <InputGroup>
          <Checkbox checked={showOnlyOwned} onChange={handleOnCheck} />
          <Label className="md:w-full mr-6 text-left text-gray-300">Only show owned NFT's</Label>
        </InputGroup>
      </div></div>
  );
}
