import React from 'react';

interface TableRowProps
  extends React.DetailedHTMLProps<
    React.TableHTMLAttributes<HTMLTableRowElement>,
    HTMLTableRowElement
  > {
  children?: React.ReactNode
}
export default function HeadTr({ children, ...props }: TableRowProps) {
  return (
    <tr className="rounded-l-lg mb-2 border-grey-light border-b" {...props}>
      {children}
    </tr>
  );
}





