import { roundTo } from 'round-to';

export function round(value: number, decimals: number = 2): number {
  return roundTo(value, decimals);
}

export function rangeBetween(start: number, end: number): Array<number> {
  const range = [];
  for (let i: number = start; i <= end; i += 1) {
    range.push(i);
  }
  return range;
}

export function sum(array: Array<number>, decimals: number = 2): number {
  return array.reduce((a, b) => a + b, 0);
}

export function sumVertically(array: Array<Array<number>>): Array<number> {
  const sum = (r, a) => r.map((b, i) => a[i] + b);

  return array.reduce(sum);
}
