import React, { useContext } from 'react';

import useStaking from '../hooks/useStaking';
import { useWallet } from '../context/wallet';
import { rangeBetween, round } from '../../utils/math';

import { AddressBar } from '../UI/organisms';
import { StakingContext } from '../context/staking';
import SimpleTable from '../UI/molecules/simple-table';
import { Heading } from '../UI/atoms/headings';
import { Alert } from '../UI/atoms';
import { Type } from '../UI/atoms/alert';

function NFTStaking() {
  const { stakingFromEpoch, showOnlyOwned } = useContext(StakingContext);
  const { calculateStakingRewards, epochs } = useStaking();
  const { countNftsByName, nfts } = useWallet();
  const { total, base, bonus } = calculateStakingRewards(stakingFromEpoch, showOnlyOwned, countNftsByName);

  const headers = ['Asset Name', 'Quantity Staked', ...(rangeBetween(epochs.rewards.start, epochs.rewards.end)), "Total"];

  return (
    <>
      <Heading level={2} center>Rewards Estimator <span className="italic">(NFT Rewards Only)</span></Heading>
      <Alert type={Type.warning}>
        DISCLAIMER: This calculator assumes all assets in wallet were delegated in the same epoch. If your assets were delegated in different epochs, you must calculate those separately
      </Alert>

      <AddressBar />

      <Heading level={3}>Total Rewards</Heading>
      <SimpleTable headers={headers} body={total}>
        {(value, index) => index > 1 ? round(value, 1).toFixed(1) : value}
      </SimpleTable>
      <div className='p-5'></div>

      <Heading level={3}>Base Rewards</Heading>
      <SimpleTable headers={headers} body={base}>
        {(value, index) => index > 1 ? value.toFixed(1) : value}
      </SimpleTable>
      <div className='p-5'></div>

      <Heading level={3}>Bonus Rewards</Heading>
      <SimpleTable headers={headers} body={bonus}>
        {(value, index) => index > 1 ? round(value, 1).toFixed(1) : value}
      </SimpleTable>
    </>
  );
}

export default NFTStaking;
