import { useCallback } from 'react';
import { StakingAssetRewards, StakingReference } from '../../models/Staking';
import * as reference from '../../assets/data/reference.json';
import { round, sum, sumVertically } from '../../utils/math';

const calculateRewards = (
  rewards: StakingAssetRewards[],
  stakingFromEpoch: number,
  epochWaitPeriod: number,
  showOnlyOwned: boolean,
  quantityFn: Function,
  calculationFn: Function,
) => {
  // Keep track of hidden rows so we can keep accurate row count.
  let hidden = 0;
  const output = [] as Array<any>;

  // eslint-disable-next-line implicit-arrow-linebreak
  rewards.forEach((reward: StakingAssetRewards, rowIndex) => {
    const quantity = quantityFn(reward.display_name);
    const epochs = Object.keys(reward.epochs);

    if (showOnlyOwned && quantity === 0) {
      hidden += 1;
      return false;
    }

    const row = [] as Array<number>;

    epochs.forEach((epoch, colIndex) => {
      const epochNo = parseInt(epoch, 10);
      const canReceiveRewards = epochNo >= stakingFromEpoch + epochWaitPeriod;

      const value = !canReceiveRewards
        ? 0
        : // Add 2 to account for name + quantity columns
          calculationFn(
            quantity,
            reward,
            epochNo,
            rowIndex - hidden,
            colIndex + 2,
          );

      row.push(value);
    });

    // Calculate totals
    row.push(sum(row));

    // Push rows to output array
    output.push([reward.display_name, quantity, ...row]);
  });
  return output;
};

export default function useStaking() {
  const epochWaitPeriod = 4;
  const { rewards, epochs } = reference as StakingReference;

  const calculateStakingRewards = useCallback(
    (
      stakingFromEpoch: number,
      showOnlyOwned: boolean,
      quantityFn: Function,
    ) => {
      // Calculate total awards
      const total = calculateRewards(
        rewards,
        stakingFromEpoch,
        epochWaitPeriod,
        showOnlyOwned,
        quantityFn,
        (quantity, reward, epochNo, rowIndex, colIndex) => {
          return quantity * reward.epochs[epochNo];
        },
      );

      // Calculate base
      const base = calculateRewards(
        rewards,
        stakingFromEpoch,
        epochWaitPeriod,
        showOnlyOwned,
        quantityFn,
        (quantity, reward, epochNo, rowIndex, colIndex) => {
          const totalColumnIndex = total[rowIndex].length - 2;
          return quantity * total[rowIndex][totalColumnIndex];
        },
      );

      // Calculate bonus
      const bonus = calculateRewards(
        rewards,
        stakingFromEpoch,
        epochWaitPeriod,
        showOnlyOwned,
        quantityFn,
        (quantity, reward, epochNo, rowIndex, colIndex) => {
          const epochsLeft = epochs.rewards.end - epochNo;
          const bonusPercent = 100 * (0.2 * epochsLeft ** 0.5);
          const bonusBase = base[rowIndex][colIndex];

          return (bonusBase / 100) * bonusPercent;
        },
      );

      // const vTotal = sumVertically(total);
      // vTotal[0] = 'Total Rewards';
      // vTotal[1] = null;

      return {
        base,
        bonus,
        total,
        // vTotal,
      };
    },
    [],
  );

  return {
    calculateStakingRewards,
    rewards,
    epochs,
  };
}
