import React from 'react';
import classNames from 'classnames';

export enum Type {
  warning,
  success,
}

type AlertProps = {
  children?: React.ReactNode;
  title?: string;
  type: Type;
};

export default function Alert({ children, type, title }: AlertProps) {
  const containerClasses = classNames({
    'border-t-4 rounded px-4 py-3 shadow-md mb-5': true,
    'bg-red-100 border-red-500 text-red-900': type === Type.warning,
    'bg-teal-100 border-teal-500 text-teal-900': type === Type.success,
  });

  const iconClasses = classNames({
    'fill-current h-6 w-6 mr-4': true,
    'text-red-900': type === Type.warning,
    'text-teal-900': type === Type.success,
  });

  return (
    <div className={containerClasses} role="alert">
      <div className="flex items-center">
        <div className="py-1">
          <svg
            className={iconClasses}
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 20 20"
          >
            <path d="M2.93 17.07A10 10 0 1 1 17.07 2.93 10 10 0 0 1 2.93 17.07zm12.73-1.41A8 8 0 1 0 4.34 4.34a8 8 0 0 0 11.32 11.32zM9 11V9h2v6H9v-4zm0-6h2v2H9V5z" />
          </svg>
        </div>
        <div>
          <p className="font-bold">{title}</p>
          <p className="text-sm">{children}</p>
        </div>
      </div>
    </div>
  );
}
