import React from 'react';

import { Table, TBody, TBodyTr, Td, Th, THead, THeadTr } from '../../atoms/table';

type SimpleTableProps = {
  headers?: Array<any>,
  body?: Array<Array<any>>,
  children?: Function
};

export default function SimpleTable({ headers, children, body }: SimpleTableProps) {
  return (
    <Table>
      {headers && <THead>
        <THeadTr>
          {
            headers.map((header) => (
              <Th>
                {header}
              </Th>
            ))
          }
        </THeadTr>
      </THead>}
      <TBody>
        {
          body && body.map((row) => (
            <TBodyTr>
              {row.map((value, index) => <Td>{children ? children(value, index) : value}</Td>)}
            </TBodyTr>
          ))
        }
      </TBody>
    </Table>
  )
}