import React from 'react';
import classNames from 'classnames';
import { HeaderProps } from './header-props';

export default function Heading({ children, center = false, level = 1 }: HeaderProps) {
  const classes = classNames({
    'font-medium leading-tight mt-0 mb-5': true,
    'text-4xl': level === 1,
    'text-3xl': level === 2,
    'text-2xl': level === 3,
    'text-xl': level === 4,
    'text-center': center
  });

  const Tag = `h${level}` as keyof JSX.IntrinsicElements;

  return (
    <Tag className={classes}>
      {children}
    </Tag>
  );
}
