import React from 'react';

interface TableBodyRowProps
  extends React.DetailedHTMLProps<
    React.TableHTMLAttributes<HTMLTableRowElement>,
    HTMLTableRowElement
  > {
  children?: React.ReactNode
}
export default function BodyTr({ children, ...props }: TableBodyRowProps) {
  return (
    <tr className="mb-2 hover:bg-stone-800 border-grey-light border-b" {...props}>
      {children}
    </tr>
  );
}


