import React from 'react';

interface TableBodyProps
  extends React.DetailedHTMLProps<
    React.TableHTMLAttributes<HTMLBodyElement>,
    HTMLBodyElement
  > {
  children?: React.ReactNode
}

export default function TBody({ children, ...props }: TableBodyProps) {
  return (
    <tbody className="" {...props}>
      {children}
    </tbody>
  );
}


