import React, { useState, useContext, useMemo, useEffect } from 'react';
import { Nfts } from '../../../models/Wallet';
import { AssetType } from '../../../models/Asset';

// List of all NFTs
import nftRecords from '../../../assets/data/nfts.json';
import axios from 'axios';
import useLocalStorage from '../../hooks/useLocalStorage';

type WalletContextProps = {
  address: string,
  setAddress: React.Dispatch<any>,
  nfts: Nfts[],
  countNftsByName: React.Dispatch<string>
};

// TODO:: This function merely merges display_name into the owned NFT list.
// Display name is used to map NFTS to staking rewards
// This is because the API does not provide this information.
// Is there a better way of achieving this.
function updateNftData(list: Nfts[]): Nfts[] {
  return list.map((nft) => {
    const asset = (nftRecords as AssetType[]).find((record: AssetType) => {
      if (record.asset_name === nft.asset_name) return record;

      return false;
    });

    return {
      ...nft,
      display_name: asset?.display_name,
    };
  });
}

const WalletContext = React.createContext<WalletContextProps | null>(null);

function WalletContextProvider({ children }: { children: React.ReactNode }) {
  // TODO:: Save to local storage
  useLocalStorage
  const [address, setAddress] = useLocalStorage('uca-bech32', '');
  const [nfts, setNfts] = useState<Nfts[]>([]);

  const countNftsByName = (displayName: string): number => {
    const found = nfts.filter((nft) => nft.display_name === displayName);
    return found.length;
  };

  const context = useMemo(() => ({
    address, nfts, setAddress, countNftsByName
  }), [address, nfts]);

  useEffect(() => {
    if (address && address.trim()) {
      setNfts([]);
      // setLoading(true);
      // setError(null);

      // Retrieve user information
      axios
        .get(`https://api.koios.rest/api/v0/account_assets?_address=${address}`)
        .then(({ data }) => {
          const uData = updateNftData(data as Nfts[]);
          setNfts(uData);
        })
        .catch(({ response }) => {
          // setLoading(false);
          // setError(response.data.message);
        });
    }
  }, [address]);

  return (
    <WalletContext.Provider value={context}>
      {children}
    </WalletContext.Provider>
  );
}

function useWallet(): WalletContextProps {
  const context = useContext(WalletContext);

  if (context === undefined) {
    throw new Error('useWallet must be used within Wallet Provider');
  }

  return context;
}

export { WalletContextProvider, WalletContext, useWallet };
